* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body{
  font-family: cursive, Arial, Helvetica, sans-serif;
  font-size: small;
  color: black;
}

.App{
  min-height: 603px;
}

.menu-container {
  display: flex;
  justify-content: center;
  min-width: 100%;
  background-image: linear-gradient(to bottom left, #43cea2, #185a9d);
}

.menu {
  width: 80%;
  display: flex;
  justify-content: space-between;
  margin: 0px 10px 0px 10px;
}
.logo{
  display: flex;
  align-items: center;
  font-weight: bold;
}

.links {
  display: flex;
  justify-content: flex-end;
}

.nav-links{
  font-weight: bold;
  padding: 25px 0;
}

.nav-links > a.active {
  color: #185a9d;
}

.nav-links:hover {
  background-image: linear-gradient(to bottom right, #185a9d, #43cea2);
}

.nav-links > a {
  text-decoration: none;
  padding: 2px;
  color: black;
}
.login {
  padding-left: 10px;
  padding-right: 10px;
}

.grid-container{
  display: flex;
  min-height: 494.5px;
  justify-content: center;
  align-items: center;
  margin: 20px 10px;
  
}

.data-form{
  display: flex;
  flex-direction: column;
  min-width: 700px;
  margin-top: 30px;
  width: 85%;
  border-radius: 8px;
  padding: 15px;
  box-shadow: 0px 2px 10px 0px #185a9d;
}

.login-form,
.signup-form, 
.loan-form{
  display: flex;
  flex-direction: column;
  min-width: 300px;
  width: 35%;
  height: max-content;
  border-radius: 8px;
  padding: 20px;
  box-shadow: 0px 0px 9px 0px grey;
}

.error{
  color: red;
  font-weight: bold;;
}

header{
  display: flex;
  justify-content: center;
  margin: 5px;
}

.signup-form > form {
  display: flex;
  flex-direction: column;
  padding: 5px;
}

.form-row {
  margin-bottom: 15px;
  display: flex;
  justify-content: flex-start;
  flex-direction: column;
  flex-wrap: wrap;
}

.form-row label {
  margin-bottom: 5px;
}

.form-row input[type='text'],
input[type='number'],
input[type='email'],
input[type='password'],
select {
  /*background-image: linear-gradient(to bottom right, #185a9d, #43cea2);*/
  border: 1px solid #43cea2;
  box-shadow: 0px 0px 9px 0px lightgrey;
  border-radius: 3px;
  width: 100%;
  padding: 7px;
  font-size: 14px;
}

.form-row input[type='text']:hover,
input[type='number']:hover,
input[type='email']:hover,
input[type='password']:hover,
select:hover {
  /*background-image: linear-gradient(to bottom right, #185a9d, #43cea2);*/
  box-shadow: 1px 0px 10px 3px lightgrey;
}

.form-row button {
  background-color: white;
  font-size: 16px;
  font-weight: bold;
  width: 65%;
  align-self: center;
  color: black;
  /*background-image: linear-gradient(to bottom right, #185a9d, #185a9d);*/
  border: 1px solid #43cea2;
  border-radius: 3px;
  padding: 10px 40px;
  cursor: pointer;
}

.form-row button:hover {
  background-color: #43cea2;
}

.form-row button:active {
  background-color: #407FC7;
}

table {
  width: 100%;
  margin: 10px;
  border-radius: 6px;
}

table,
th,
td {
  border: 1px solid #407FC7;
  border-collapse: collapse;
}

th,
td { 
  padding: 10px;
}

th {
  background-image: linear-gradient(to bottom right, #185a9d, #185a9d);
  padding: 2px;
}

.pending {
  color: white;
  width: 80px;
  background-color: orange;
  font-weight: bold;
  padding: 8px;
  border-radius: 5px;
}

.approved {
  color: white;
  width: 80px;
  background-color: olivedrab;
  font-weight: bold;
  padding: 8px;
  border-radius: 5px;
}

.unverified{
  color: white;
  width: 80px;
  background-color: red;
  font-weight: bold;
  padding: 8px;
  border-radius: 5px;
}

.verified {
  color: white;
  width: 80px;
  background-color: green;
  font-weight: bold;
  padding: 8px;
  border-radius: 5px;
}


.footer-container{
  display: flex;
  justify-content: center;
  align-items: center;
  min-width: 100%;
  bottom: 0;
  color: white;
  background-image: linear-gradient(to bottom right, #43cea2, #185a9d);
  min-height: 60px;
}

.footer{
  display: flex;
}

.modal {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.3);
}

.modal-main {
  position: fixed;
  background-image: linear-gradient(to bottom right, #185a9d, #43cea2);
  padding: 25px;
  width: 60%;
  height: auto;
  top: 50%;
  left: 50%;
  border-radius: 9px;
  transform: translate(-50%, -50%);
}

.display-block {
  display: block;
}

.display-none {
  display: none;
}

/* Profile Menu */


.dropdown {
  position: relative;
  list-style: none;
}

.dropdown>span {
  z-index: 2;
  position: relative;
  /* This is important! */
  cursor: pointer;
}

.features-menu {
  display: none;
  flex-direction: column;
  background-image: linear-gradient(to bottom right, #185a9d, #43cea2);
  border-radius: 5px;
  padding-top: 60px;
  position: absolute;
  top: -25px;
  z-index: 1;
  
}

.dropdown:hover .features-menu {
  display: flex;
  flex-direction: column;
  background-image: linear-gradient(to bottom right, #185a9d, #43cea2);
  border-radius: 5px;
  padding-top: 60px;
  position: absolute;
  top: -25px;
  right: -25px;
  z-index: 1;
}

.features-menu li {
  list-style: none;
  border-bottom: 1px solid #FFF;
  padding: 0 40px 10px 20px;
  margin-top: 20px;
}

.features-menu li a {
  color: black;
  text-decoration: none;
}

.features-menu li:last-of-type {
  border-bottom: none;
}
